<template>
  <div class="container my-5 px-4 px-lg-5">
    <div class="row gx-4 gx-lg-5 justify-content-center">
      <div class="col-md-10 col-lg-8 col-xl-7">
        <h1></h1>
        <div class="my-5">
          <form id="userform" method="post" action="">
            <div class="form-floating">
              <input
                class="form-control"
                id="name"
                type="text"
                v-model="name"
                placeholder=" ."
              />
              <label for="name">Name</label>
            </div>
            <div class="form-floating">
              <input
                class="form-control"
                id="id"
                type="text"
                v-model="id"
                placeholder="ID ."
              />
              <label for="id">ID</label>
              <div id="idresult" class="mt-1"></div>
            </div>
            <div class="form-floating">
              <input
                class="form-control"
                id="pass"
                type="password"
                v-model="pass"
                placeholder=" ."
              />
              <label for="pass">Password</label>
            </div>
            <div class="form-floating">
              <input
                class="form-control"
                id="passcheck"
                type="password"
                v-model="passcheck"
                placeholder="  ."
              />
              <label for="pass">Password Check</label>
            </div>
            <br />
            <!-- Submit Button-->
            <b-button variant="outline-primary" @click="checkValue"
              >Sign Up</b-button
            >&nbsp;
            <b-button variant="outline-danger" @click="resetValue"
              >Cancel</b-button
            >
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import http from "@/config/http-common.js";

export default {
  name: "Signup",
  data() {
    return {
      name: "",
      id: "",
      pass: "",
      passcheck: "",
    };
  },

  methods: {
    //   -   registBook 
    checkValue() {
      //   
      // isbn, , , ,        
      let err = true;
      let msg = "";
      !this.name &&
        ((msg = " ."),
        (err = false)) /*, this.$refs.name.focus()*/;
      err && !this.id && ((msg = "ID ."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        !this.pass &&
        ((msg = " ."), (err = false)) /*,
        this.$refs.id.focus()*/;
      err &&
        this.pass != this.passcheck &&
        ((msg = "  "), (err = false));

      if (!err) alert(msg);
      else this.registMember();
    },

    registMember() {
      http
        .post(`/users`, {
          userName: this.name,
          userId: this.id,
          password: this.pass,
        })
        .then(({ data }) => {
          //      .
          let msg = "  .";
          if (data === "success") {
            msg = " .";
          }
          alert(msg);
          this.$router.push({ name: "Home" });
        });
    },
    resetValue() {
      this.$router.push("/");
    },
  },
};
</script>

<style></style>
