<template>
  <div class="container">
    <h1 class="fw-bold mt-5 mb-3 title-deco">{{ userInfo.userName }}'s </h1>
    <b-row class="bCon justify-content-center">

      <b-card bg-variant="light" class="col-4">
        <b-avatar
          variant="primary"
          src="https://dcimg4.dcinside.co.kr/viewimage.php?id=26b4ca33ebd339af&no=24b0d769e1d32ca73ceb84fa11d02831abf8dd37dbca42c9c56298cca8560265a7d8652086bfe4b9343f67919400852e8de3921a268bec3508394fa2d71e05d88e6ff22c1eb5a3174ddf19d920114166da3bce0caad0a7b3d2c982290a031781"
          size="10rem"
        ></b-avatar>
        <h5 class="my-3">{{ userInfo.userName }}</h5>
        <!-- <h5> : {{ userInfo.user_name }}</h5> -->
        <h5> : {{ userInfo.userId }}</h5>
        <h5>   :</h5>
        <h5> :</h5>
        <h5>:</h5>

        <!--   -->
        <b-button v-b-modal.modal2-prevent-closing @click="getUserData()"> </b-button>
            <b-modal
              id="modal2-prevent-closing"
              ref="modal"
              title=" "
              hide-footer
              v-if="modalCheck"
            >
              <form ref="form">
                <b-form-group
                  label=" username"
                  label-for="user_id"
                >
                  <b-form-input
                    id="username"
                    v-model="user.user_name"
                    placeholder=" username "
                  ></b-form-input>
                </b-form-group>
              </form>
              <form ref="form">
                <b-form-group
                  label=" "
                  label-for="user_pw"
                >
                  <b-form-input
                    type="password"
                    id="user_pw"
                    v-model="user.user_pw"
                    placeholder=" password "
                  ></b-form-input>
                </b-form-group>
              </form>
              <form ref="form">
                <b-form-group
                  label="  "
                  label-for="user_pw_check"
                >
                  <b-form-input
                    type="password"
                    id="user_pw_check"
                    v-model="user.user_pw_check"
                    placeholder=" password "
                    @keyup.enter="userInfoUpdate()"
                  ></b-form-input>
                </b-form-group>
              </form>
              <hr />
              <b-button @click="userInfoUpdate()">  </b-button>
            </b-modal>
  

        <b-button @click="deleteUser"></b-button>
      </b-card>
    </b-row>
    
    <b-row class="pyo justify-content-center mt-4">
      <b-row><p class="text-center fs-5 fw-bold"></p></b-row>
      <b-progress
        :value="marathon.value"
        :max="marathon.max"
        show-progress
        class="mb-3 col-4"
      ></b-progress>
    </b-row>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";
import { mapActions } from "vuex";
import http from "@/config/http-common.js";


const userStore = "userStore";
export default {
  data() {
    return {
      marathon: {
        value: 33.333333,
        max: 50,
      },
      user: {
        user_name: "",
        user_pw: "",
        user_pw_check: ""
      },
      modalCheck: false,
    };
  },
  methods: {
    ...mapMutations(userStore, ["SET_IS_LOGIN", "SET_USER_INFO"]),
    ...mapActions(userStore, ["getUserInfo"]),
    //  
    deleteUser() {
      // 
      if (confirm('?')) {
        http({
        method: 'delete',
        url: `/users/${this.userInfo.userId}`,
        })
        .then((response) => {
          console.log(response);
          // 
          this.SET_IS_LOGIN(false);
          this.SET_USER_INFO(null);
          this.$router.push({
            path: "/",
          })
          alert(' ')
        })
        .catch((err) => {
          console.dir(err);
        })
      } else {
        alert(' ')
      }
    },
    getUserData() { //   
      this.modalCheck = true
      this.user.user_name = this.userInfo.userName
    },
    checkInfo() { //   
      if(this.user.user_pw != this.user.user_pw_check) {
        alert('  ')
        return false
      } else if(this.user.user_pw === '' && this.user.user_pw_check == '') {
        alert(' password ')
        return false
      } else if(this.user.user_name === '') { //   
        alert(' username ')
        return false
      } else if(this.user.user_name === this.userInfo.userName ) {
        if (confirm('username  ?')) {
          return true
        } else {
          return false
        }
      } else {
        return true
      }

    },
    userInfoUpdate() { //  
      if(this.checkInfo()) {
        http({
        method: 'put',
        url: `/users/${this.userInfo.userId}`,
        data: {
          'password': this.user.user_pw,
          'userId': this.userInfo.userId,
          'userName': this.user.user_name
          }
        })
          .then((response) => {
            console.log(response)
            alert('  ')

            //         ( userStore )
            let token = sessionStorage.getItem("access-token");
            this.getUserInfo(token)
           
            //       
            this.user.user_pw = ""
            this.user.user_pw_check = ""
            this.modalCheck = false
          })
          .catch((err) => {
            console.dir(err)
          })
      }
    }

  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
  },
};
</script>

<style>

/* .bCon {
  width: 600px;
  margin-top: 20px;
} */

.info {
  width: 600px;
  border: solid;
  /* float: left; */
}

.pyo {
  margin-top: 100px;
}

.title-deco {
  background: linear-gradient(to right, #A7A3FF, #FFA7A3, #671cc4, #5673bd); 
  -webkit-background-clip: text; 
  -webkit-text-fill-color: transparent;
}
</style>
