<template>
  <div class="col">
    <b-card no-body bg-variant="light" class="overflow-hidden mx-2 my-2" style="max-width: 540px;">
      <b-row no-gutters>
        <b-col md="6">
          <b-card-img :src="thumbnail_url" alt="Image" class="rounded-3 mt-3 ms-3"></b-card-img>
        </b-col>
        <b-col md="6" class="mb-3">
          <b-card-body :title="item.title"> <!-- -->
            <b-card-text>
              <p class="card-text">: {{ item.id }}</p> <!-- -->
              <p class="card-text">: {{ item.bookDetail.id }}</p> <!--bookStore    ..?-->
              <p class="card-text" v-if="item.conferenceType.id==1">: </p> <!---->
              <p class="card-text" v-if="item.conferenceType.id==2">: </p> <!---->
              <p class="card-text" v-if="item.conferenceType.id==3">: </p> <!---->
              <p class="card-text">: {{ item.callStartTime }}</p>
              <p class="card-text">: {{ item.callEndTime }}</p>
              <p v-if="item.isActive" class="card-text"> : <span class="text-success"></span> </p> 
              <p v-else class="card-text"> : <span class="text-danger"></span></p> 
              <p class="card-text">  : {{ item.participant }}</p>
              
            </b-card-text>
          </b-card-body>
          <b-button variant="primary" @click="enterConference" v-if="item.isActive"></b-button> <!-- id  detail  -->
          <p class="badge bg-dark text-wrap fs-6 mb-1" v-else style="padding: 12px;"></p> <!-- id  detail  -->
        </b-col>
      </b-row>
    </b-card>
  </div>


</template>

<script>
import { mapState, mapActions } from "vuex";

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: 'ConferenceListItem',
  props: {
    item: {
      type: Object,
      required: true,
    },
  },
  computed: {
    ...mapState(userStore, ["userInfo"]),
    ...mapState(conferenceStore, ["conferenceDetail"]),
  },
  methods: {
    ...mapActions("conferenceStore", ["createRoom", "getConferenceCategories"]),
    enterConference() {
      //   
      this.getConferenceCategories()
      this.$router.push({
        path: `conference/view/${this.item.id}`,
      });
    }
  },
  data: function () {
    return {
      thumbnail_url: require('@/assets/thumbnail/thumbnail_default_img.png') //  thumbnail ....
      // thumbnail_url: `https://iamge/${this.item.thumbnail_url}`,
    }
  }
}
</script>

