<template>
  <div>
    <b-button 
    v-b-modal.modal3-prevent-closing 
    variant="primary" 
    @click="$bvModal.show('modal-scoped'); beforeRoomInfo()"
    >  </b-button>
      <b-modal
      id="modal3-prevent-closing"
      ref="modal"
      title="  "
    >
      <form ref="form">
        <b-form-group
          class="roommodal"
          id="dropdown-1"
          label=""
          label-for="usage-roominput"
        >
          <b-form-select v-model="selectedOption" :options="options">
            <template #first>
              <b-form-select-option :value="null" disabled>-- --</b-form-select-option>
            </template>
          </b-form-select>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          class="roommodal"
          label=" "
          label-for="name-roominput"
        >
          <b-form-input
            id="name-input"
            v-model="roomName"
            placeholder="   "
          ></b-form-input>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="textarea"
          class="roommodal"
          label=""
          label-for="content-roominput"
        >
          <b-form-textarea
            id="textarea"
            v-model="roomContent"
            placeholder="   "
            row="3"
            max-rows="6"
          ></b-form-textarea>
        </b-form-group>
      </form>
      <form ref="form">
        <b-form-group
          type="file"
          class="roommodal"
          label=""
          label-for="thumbnail-roominput"
        >
          <b-form-file
            accept=".png, .jpg, .jpeg, .gif"
            v-model="thumbnailFile"
            class="mt-3"
            plain
          ></b-form-file>
          <!-- <div class="mt-3">Selected file: {{ thumbnailFile ? thumbnailFile.name : "" }}</div> -->
        </b-form-group>
      </form>
      <br />
      <template #modal-footer="{ ok, cancel }">
        <b-button variant="outline-primary" class="me-2" @click="roomUpdate() ? ok() : _">  </b-button>
        <b-button @click="cancel()">  </b-button> 
      </template>

      
    </b-modal>
  </div>
</template>

<script>
import { mapState, mapActions } from "vuex";
import Swal from "sweetalert2";

const userStore = "userStore";
const conferenceStore = "conferenceStore";

export default {
  name: "ConferenceDetailUpdate",
  computed: {
    ...mapState(userStore, ["userInfo"]),
    ...mapState(conferenceStore, ["conferenceDetail"]),
  },
  created() {
  },
  data() {
    return {
      conferenceId: this.$route.params.contentId,
      roomName: "",
      roomContent: "", //  -  DB     :  
      thumbnailFile: "",
      selectedOption: null,
      options : [
        { value: '1', text: '' },
        { value: '2', text: '' },
        { value: '3', text: '' },
      ]
    };
  },
  methods: {
    ...mapActions(conferenceStore, ["conferenceUpdate", "getConferenceInfo"]),
    beforeRoomInfo() {
      // this.getConferenceInfo(this.$route.params.contentId); 
      // this.getConferenceInfo(this.conferenceId); 
      this.roomName = this.conferenceDetail.title,
      this.roomContent = this.conferenceDetail.description, //  -  DB     :  
      this.thumbnailFile = "", //  - thumbnail file    
      this.selectedOption = this.conferenceDetail.conferenceType.id
    },
    roomIsValid: function () {
      if (
        this.selectedOption === null ||
        this.roomName === ""
      ) {
        Swal.fire({
          icon: "error",
          title: "Stop!",
          text: ",   .",
        });
        return false
      } else if (this.roomName.length > 31) {
        Swal.fire({
          icon: "error",
          title: "RoomnameError",
          text: "  30  .",
        });
        return false
      } else if (this.thumbnailFile.size > 2097152) { //size byte 
        Swal.fire({
          icon: "error",
          title: "ImgsizeError",
          text: "     2MB.",
        });
        return false
      } else {
        console.log(this.selectedOption);
        return true
      }
    },
    roomData() {
      return {
        id: this.conferenceId,
        user: { 
          userId: this.userInfo.userId
        },
        bookDetail: {
          id: 1, 
        }, //  - book DB  
        conferenceType: { 
          id: this.selectedOption,
        },
        thumbnailUrl: this.thummbnailFile, //   -   
        title: this.roomName,
        description: this.roomContent,
        //  ??  ???   
        // isActive: 1
      }
    },
    roomUpdate() {
      if (this.roomIsValid()) {
        this.conferenceUpdate(this.roomData()) //   //   payload 
        console.log(this.roomData())
        return true
      }
      else {
        return false
      }
    },
  },
};
</script>

